<template>
  <div class="recruitmentBox">
    <div class="recruitment">
      <div class="title">已发布</div>
      <div class="addRecruitment">
        <el-button type="success" plain @click="dialogFormVisible = true">添加</el-button>
      </div>
      <div class="recruitmentList">
        <el-table :data="recruitmentList" style="width: 100%" height="500">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="招聘公司/企业">
                  <span>{{ props.row.corporateName }}</span>
                </el-form-item>
                <el-form-item label="招聘岗位">
                  <span>{{ props.row.recruitmentPost }}</span>
                </el-form-item>

                <el-form-item label="工作地点">
                  <span>{{ props.row.workPlace }}</span>
                </el-form-item>
                <el-form-item label="招聘人数">
                  <span>{{ props.row.recruitingNumbers }}</span>
                </el-form-item>
                <el-form-item label="招聘时间">
                  <span>{{ props.row.recruitingTime.split(",")[0]}} ~ {{ props.row.recruitingTime.split(",")[1]}}</span>
                </el-form-item>
                <el-form-item label="招聘地点">
                  <span>{{ props.row.recruitingAddress }}</span>
                </el-form-item>
                <el-form-item label="薪资待遇">
                  <span>{{ props.row.salary }}</span>
                </el-form-item>
                <el-form-item label="投递地址">
                  <a :href="props.row.deliveryUrl" target="view_window" style="color:#481aee">我要投递</a>
                </el-form-item>
                <el-form-item label="职位要求">
                  <el-popover
                    placement="top-start"
                    width="200"
                    trigger="hover"
                    :content="props.row.jobRequirements"
                  >
                    <span slot="reference" style="color:red">预览</span>
                  </el-popover>
                </el-form-item>
                <el-form-item label="公司简介 ">
                  <el-popover
                    placement="top-start"
                    width="200"
                    trigger="hover"
                    :content="props.row.companyProfile"
                  >
                    <span slot="reference" style="color:red">预览</span>
                  </el-popover>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column label="招聘公司/企业" prop="corporateName"></el-table-column>
          <el-table-column label="招聘岗位" prop="recruitmentPost"></el-table-column>
          <el-table-column label="招聘人数" prop="recruitingNumbers"></el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <recruitmentDialog :userInfo="userInfo" :dialogFormVisible="dialogFormVisible" @dialogClose="dialogClose"></recruitmentDialog>
      </div>
    </div>
  </div>
</template>
<script>
import {
  interRecruitMentInfo,
  deleteRecruitMentInfo
} from "../../../../../utils/employment.url";
import recruitmentDialog from "./recruitmentDialog.vue";
export default {
  components: { recruitmentDialog },
  data() {
    return {
      userInfo: {},
      recruitmentList: [],
      dialogFormVisible: false
    };
  },
  methods:{
    //获取招聘信息列表
    getRecruitMentInfo() {
      interRecruitMentInfo({ counselorNo: this.userInfo.userId }, "get")
        .then(res => {
          if (res.status == 200) {
            this.recruitmentList = res.data;
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    handleDelete(index, row) {
      this.$confirm("此操作将永久删除该条数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          deleteRecruitMentInfo(row, "delete").then(res => {
            if (res.status == 200) {
              this.getRecruitMentInfo();
              this.$message({
                message: res.message,
                type: "success"
              });
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    //关闭弹框
    dialogClose(value){
      this.dialogFormVisible = value;
      this.getRecruitMentInfo()
    }
  },
  created(){
    this.userInfo = JSON.parse(sessionStorage.getItem("userInfo"));
    this.getRecruitMentInfo()
  }
};
</script>
<style lang="scss" scoped>
.recruitmentBox {
  width: 1000px;
  margin: 20px auto;
  .recruitment {
    .title,
    .addRecruitment {
      display: inline-block;
    }
    .addRecruitment {
      float: right;

      .el-button {
        width: 56px;
        height: 28px;
        padding: 0px;
      }
    }
    .title {
      font-size: 16px;
      font-weight: 600;
      margin: 10px;
    }
    .recruitmentList {
      .el-table {
        border-radius: 10px;
      }
    }
  }
}
.demo-table-expand {
  font-size: 0;
}
.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
  font-size: 18px;
  font-family: "华文新魏";
  span {
    font-family: "Times New Roman", Times, serif;
  }
}
</style>