<template>
  <div class="calssDialogBox">
    <el-dialog
      title="发布招聘信息"
      :visible="dialogFormVisible"
      v-if="dialogFormVisible"
      @close="dialogClose"
    >
      <el-form :model="recruitmentForm" :rules="rules" ref="recruitmentForm">
        <el-form-item label="招聘公司/企业" :label-width="formLabelWidth" prop="corporateName">
          <el-input v-model="recruitmentForm.corporateName" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="招聘岗位" :label-width="formLabelWidth" prop="recruitmentPost">
          <el-input v-model="recruitmentForm.recruitmentPost" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="工作地点" :label-width="formLabelWidth" prop="workPlace">
          <el-input v-model="recruitmentForm.workPlace" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="招聘人数" :label-width="formLabelWidth" prop="recruitingNumbers">
          <el-input v-model="recruitmentForm.recruitingNumbers" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="招聘时间" :label-width="formLabelWidth" prop="recruitingTime">
          <template>
            <el-date-picker
              v-model="recruitmentForm.recruitingTime"
              value-format="yyyy-MM-dd HH:mm:ss"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            ></el-date-picker>
          </template>
        </el-form-item>
        <el-form-item label="招聘地点" :label-width="formLabelWidth" prop="recruitingAddress">
          <el-input v-model="recruitmentForm.recruitingAddress" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="职位要求" :label-width="formLabelWidth" prop="jobRequirements">
          <el-input
            v-model="recruitmentForm.jobRequirements"
            autocomplete="off"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
          ></el-input>
        </el-form-item>
        <el-form-item label="公司简介" :label-width="formLabelWidth" prop="companyProfile">
          <el-input
            v-model="recruitmentForm.companyProfile"
            autocomplete="off"
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 4}"
          ></el-input>
        </el-form-item>
        <el-form-item label="薪酬待遇" :label-width="formLabelWidth" prop="salary">
          <el-input v-model="recruitmentForm.salary" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="投递地址" :label-width="formLabelWidth" prop="deliveryUrl">
          <el-input v-model="recruitmentForm.deliveryUrl" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelEdit">取 消</el-button>
        <el-button type="primary" @click="submitForm('recruitmentForm')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { addRecruitMentInfo } from "../../../../../utils/employment.url";
import { isNum } from "@/utils/utils";
export default {
  props: ["dialogFormVisible", "userInfo"],
  data() {
    return {
      formLabelWidth: "120px",
      recruitmentForm: {},
      rules: {
        corporateName: [
          { required: true, message: "请输入招聘企业", trigger: "blur" }
        ],
        recruitmentPost: [
          { required: true, message: "请输入招聘岗位", trigger: "blur" }
        ],
        workPlace: [
          { required: true, message: "请输入工作地点", trigger: "blur" }
        ],
        recruitingNumbers: [
          { required: true, message: "请输入招聘人数", trigger: "blur" },
          { validator: isNum, trigger: "blur" }
        ],
        recruitingTime: [
          { required: true, message: "请输入招聘时间", trigger: "blur" }
        ],
        recruitingAddress: [
          { required: true, message: "请输入招聘地点", trigger: "blur" }
        ],
        jobRequirements: [
          { required: true, message: "请输入招聘要求", trigger: "blur" }
        ],
        companyProfile: [
          { required: true, message: "请输入公司简介", trigger: "blur" }
        ],
        salary: [
          { required: true, message: "请输入薪酬待遇", trigger: "blur" }
        ],
        deliveryUrl: [
          { required: true, message: "请输入投递地址", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    dialogClose() {
      this.$emit("dialogClose", false);
    },
    //,取消
    cancelEdit() {
      // this.dialogFormVisible = false;
      this.$emit("dialogClose", false);
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.recruitmentForm.counselorNo = this.userInfo.userId;
          console.log(this.recruitmentForm)
          addRecruitMentInfo(this.recruitmentForm, "post").then(res => {
            if (res.status == 200) {
              this.$message({
                message: res.message,
                type: "success"
              });
              this.$emit("dialogClose", false);
            }
          });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>